@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:wght@400&display=swap);
*{margin:0;padding:0;box-sizing:border-box}:root{--primary: #35d0f2;--secondary: #f2e744;--black: #0d0d0d;--light: #f2f2f2;--bg: #f2c53d;--font: "MuseoModerno", cursive}body{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#f2c53d;background:var(--bg);font-family:"MuseoModerno", cursive;font-family:var(--font)}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.container{width:100%;height:100%;margin:0 auto}@media (min-width: 745px){.container{display:flex;justify-content:center;align-items:center}}header{background:var(--black);width:100%;height:auto;padding:10px 0}header h1{text-align:center;color:var(--secondary);letter-spacing:0.18em}

.board{width:90%;height:80vh;background:var(--bg);margin:20px auto;display:flex;flex-direction:column;align-items:center;justify-content:center;padding:20px}.board-status{color:var(--dark);font-size:1.3em;margin-bottom:20px;letter-spacing:0.2em}.board-row{width:100%;height:100%;display:flex;justify-content:center;align-items:center}@media (min-width: 745px){.board{width:500px;height:500px}}

.square{width:100%;height:100%;background:var(--black);font-size:3em;font-weight:bold;border:none;outline:none;cursor:pointer;border:1px solid;box-shadow:none}.xPlayer{color:var(--primary)}.oPlayer{color:var(--secondary)}

